<!-- FPV穿越无人机页面 -->
<template>
  <div class="fpv-drone-page">
    <!-- 顶部大色块banner -->
    <section class="hero-section">
      <div class="hero-image">
        <div class="hero-overlay">
          <div class="container">
            <div class="hero-content text-center">
              <h2 class="main-title mb-4">穿越无人机</h2>
              <div class="subtitle-list">
                <span>高速穿越</span>
                <span>高清FPV</span>
                <span>竞速特技</span>
                <span>航拍创作</span>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>

    <!-- 产品展示区域 -->
    <section class="products-section">
      <div class="container">
        <div class="product-grid">
          <div class="product-item" v-for="(drone, index) in drones.slice(0, 4)" :key="drone.id">
            <div class="product-frame clickable" @click="goToDetail(drone.id)">
              <div class="drone-image-placeholder" :style="{background: getColor(index)}"></div>
              <div class="product-info">
                <div class="product-code">{{drone.name}}</div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>

    <!-- 加载状态弹窗 -->
    <div class="loading-overlay" v-if="isLoading">
      <div class="loading-content">
        <div class="loading-spinner"></div>
        <p>正在加载产品信息...</p>
      </div>
    </div>

    <!-- 错误提示弹窗 -->
    <div class="error-overlay" v-if="showError">
      <div class="error-content">
        <div class="error-icon">!</div>
        <p>{{errorMessage}}</p>
        <button class="retry-button" @click="retryNavigation">重试</button>
        <button class="close-button" @click="closeError">关闭</button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'FPVDrone',
  data() {
    return {
      drones: [
        { id: 'fpv-p1', name: 'P1 竞速穿越机' },
        { id: 'fpv-p2x', name: 'P2X 特技穿越机' },
        { id: 'fpv-c1', name: 'C1 电影穿越机' },
        { id: 'fpv-x4', name: 'X4 四轴穿越机' }
      ],
      colors: ['#f7c873', '#6ad1e3', '#b6e388', '#b6b6f7'],
      isLoading: false,
      showError: false,
      errorMessage: '',
      pendingNavigation: null
    }
  },
  methods: {
    getColor(index) {
      return this.colors[index % this.colors.length]
    },
    async goToDetail(droneId) {
      this.isLoading = true
      this.pendingNavigation = droneId
      
      try {
        // 模拟检查产品详情页是否存在
        const exists = await this.checkProductExists(droneId)
        if (!exists) {
          throw new Error('产品信息未找到')
        }
        
        await this.$router.push(`/products/details/fpv/${droneId}`)
      } catch (error) {
        this.showError = true
        this.errorMessage = error.message || '加载产品信息失败，请稍后重试'
      } finally {
        this.isLoading = false
      }
    },
    async checkProductExists(droneId) {
      // 这里可以添加实际的产品信息检查逻辑
      // 例如：调用API检查产品是否存在
      return new Promise(resolve => {
        setTimeout(() => {
          resolve(true)
        }, 500)
      })
    },
    retryNavigation() {
      if (this.pendingNavigation) {
        this.showError = false
        this.goToDetail(this.pendingNavigation)
      }
    },
    closeError() {
      this.showError = false
      this.errorMessage = ''
      this.pendingNavigation = null
    }
  }
}
</script>

